import React, { useCallback, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Button, Form, Input } from 'semantic-ui-react';
import AuthCard from 'components/auth/AuthCard';
import AuthLinkCard from 'components/auth/AuthLinkCard';
import { dummyUser, loginRequest } from 'redux/user/userSlice';
import { useInput } from 'hooks/useInput';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const Auth = () => {
  const { loginLoading } = useSelector((state) => state.user);
  const dispatch = useDispatch();
  const [email, onChangeEmail] = useInput('');
  const [password, onChangePassword] = useInput('');
  const [loginError, setLoginError] = useState(false);

  const onSubmit = useCallback(() => {
    const userIndex = dummyUser.findIndex((v) => v.email === email);
    const checkPassword = dummyUser[userIndex]?.password === password;
    userIndex !== -1 || setLoginError(true);
    checkPassword || setLoginError(true);
    userIndex !== -1 && checkPassword && dispatch(loginRequest(email));
  }, [email, password]);

  return (
    <>
      <AuthCard content="ë¡œê·¸ì¸">
        <Form onSubmit={onSubmit}>
          <Input
            icon="user"
            iconPosition="left"
            type="email"
            placeholder="email@gmail.com"
            onChange={onChangeEmail}
            autoFocus
            size="small"
          />
          <Input
            icon="lock"
            iconPosition="left"
            type="password"
            placeholder="ë¹„ë°€ë²ˆí˜¸"
            onChange={onChangePassword}
            size="small"
          />
          <div>
            <s.Button size="small" content="ë¡œê·¸ì¸" color="teal" loading={loginLoading} />
          </div>
        </Form>
        {loginError && 'ì´ë©”ì¼ì´ë‚˜ ë¹„ë°€ë²ˆí˜¸ë¥¼ í™•ì¸í•´ì£¼ì„¸ìš”.'}
      </AuthCard>
      <AuthLinkCard query="ê³„ì •ì´ ì—†ìœ¼ì‹ ê°€ìš”?">
        <Link to="/signUp">ê°€ìž…í•˜ê¸°</Link>
      </AuthLinkCard>
    </>
  );
};

const s = {};
s.Button = styled(Button)`
  width: 100%;
`;
export default Auth;
